@media (max-width: 1200px) {
  .burgerMenu__content {
    width: 100%;
    left: 0;
    transform: none;
  }

  .burgerMenu__nav-item {
    max-width: 100%;
  }

  .header {
    justify-content: space-evenly;
  }

  .container {
    margin: 0;
  }

}


@media (max-width: 1100px) {
  .container {
    width: auto;
  }

  .movies-container {
    justify-content: space-around;
  }

  .card:hover {
    transform: translateY(-0px) rotateZ(0deg);

  }

  .movie-info--plot {
    width: 30%;
  }

}



@media (max-width: 768px) {
  .movies-container {
    justify-content: space-evenly;
    row-gap: 20px;
  }

  .fav-movies-container {
    justify-content: space-around;
  }

  .card {
    background: linear-gradient(to top, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.5));
    width: 260px;
    margin: 0 auto;

    &:hover {
      transform: none;
      box-shadow: none;
    }


    &::before {
      opacity: 1;
      background: none;
    }

    & img:hover {
      filter: none;
    }
  }


  .movie-info--plot {
    width: 90%;
  }

  .share-buttons {
    gap: 15px;
  }

  .movie-details {
    width: auto;
    margin-left: 10px;
    margin-right: 10px;
  }

  .logo_title {
    display: none;
  }
}




@media (max-width: 600px) {

  .burger_title {
    display: none;
  }

  .logo_container {
    margin-left: 0;

  }

  button {
    margin-left: 0;
  }

  .settings {
    width: auto;
    height: auto;
    margin: 0 10px;
    margin-top: 10px;
  }

  .settings__form-profile,
  .settings__form-password {
    width: auto;
    flex-direction: column;
  }

  .settings__form-password {
    height: auto;
  }

  .settings__form-password-lol {
    width: auto;
  }

  .settings_btn {
    width: auto;
  }



}

@media (max-width: 500px) {

  .search {
    width: 200px;
    margin-left: 10px;
    margin-right: 10px;
  }

  .search-input {
    width: 100%;
  }

  .movie-poster--img {
    margin-left: 0px;
    width: 220px;
  }

  ul,
  ol {
    padding: 0;
  }

  .subline {
    text-align: center;
  }

  .movie-info h1 {
    text-align: center;
  }

  .movie-rating {
    justify-content: center;
  }

  .logo {
    display: none;
  }

  .header {
    justify-content: center;
    padding-bottom: 25px;
  }


}



@media (max-width: 400px) {

  .burgerMenu__content.open {
    height: 100%;
  }

  .burgerMenu__nav {

    flex-direction: column;
    gap: 20px;
    margin-top: 50%;
  }

  .burgerMenu__nav-btn {
    font-size: 28px;
  }

  .previous-button {
    width: 100px;
    height: 50px;
  }

  .next-button {
    width: 100px;
    height: 50px;
  }

  .footer {
    background-color: #130429;
  }

  .burgerMenu__theme-box {
    margin-top: 50px;
  }
}